export function getStore({ builder, schema, externalBuilder, }) {
    const result = {
        dataByKeys: {},
        elementsByKeys: getElementsByKeys(schema),
    };
    for (const [key, elem] of Object.entries(result.elementsByKeys)) {
        // FIXME: поздние ключи могут использовать более ранние, но не наоборот
        result.dataByKeys[key] = builder({
            schema: [elem],
            store: result,
            externalBuilder,
        });
    }
    return result;
}
export function getElementsByKeys(schema) {
    let result = {};
    for (const item of schema) {
        if (typeof item === 'string') {
            continue;
        }
        result[item.props.key] = item;
        const childrenRecord = getElementsByKeys(item.children);
        result = Object.assign(Object.assign({}, result), childrenRecord);
    }
    return result;
}
